.home {
}
.home > section#hero {
  background-image: url("/assets/images/perfect-arepas.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#hero > .background-hero {
  background: #000b;
  min-height: calc(100vh - 4rem);
  padding: 1rem;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}
#hero > .background-hero > .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  width: 90%;
}
.hero {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100%;
}
#hero > .background-hero > .container > .form-search {
  width: 100%;
  border-radius: 0.3rem;
  padding: 1rem;
  background: #fff2;
}
.hero > h2 {
  text-align: center;

  font-size: 12vw;
  text-transform: capitalize;
}
.hero > h2 > span {
  color: var(--yellow);
}
.hero > p {
  font-size: 7vw;
  text-align: center;
}
.hero > button {
  padding: 0.5rem 2rem;
  border-radius: 2rem;
  border: 0;
  background-color: var(--pink);
  color: var(--white);
  cursor: pointer;
  font-size: 5vw;
}
.hero > button:hover {
  color: var(--pink);
  background-color: var(--white);
}

/* Last Recipes */
#recipes {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 0;
}
#recipes > .container {
  width: 90%;
  max-width: 1024px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
}
#recipes > .container > h2 {
  font-size: 1.7rem;
  font-weight: bold;
  margin-bottom: 1rem;
  margin-top: 1rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: "Poppins", sans-serif;
  border-bottom: 3px solid;
  color: #444;
}
.recipes-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
  width: 100%;
}

/* Recipes Categories */
#recipes-categories {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 0;
  background-color: rgb(241, 241, 241);
}
#recipes-categories > .container {
  width: 90%;
  max-width: 1024px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
}
#recipes-categories > .container > h2 {
  font-size: 1.7rem;
  font-weight: bold;
  margin-top: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: "Poppins", sans-serif;
  color: #444;
  border-bottom: 3px solid;
}
#recipes-categories > .container > .recipes-categories {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100%;
}
#recipes > .container > .area-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
  width: 100%;
}
#recipes > .container > .area-items > a {
  position: relative;
  background-color: #fff;
  border-radius: 0.5rem;
  width: 70%;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  justify-content: center;
}
#recipes > .container > .area-items > a > .area-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}
#recipes > .container > .area-items > a > .area-item > h3 {
  font-size: 1.5rem;
  text-align: center;
  padding-top: 1rem;
  border-top: 1px solid #ccc;
  color: #555;
  width: 100%;
}
#recipes > .container > .area-items > a > .area-item > img {
  width: 100%;
  border-radius: 50%;
  width: 50vw;
  height: 50vw;
  object-fit: cover;
}
.category-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  width: 75%;
  box-shadow: 0 0 10px #0001;
  background-color: #fff;
  border-radius: 0.5rem;
  padding: 1rem;
}

.top-category {
}
.top-category > img {
  width: 9rem;
  height: 9rem;
  border-radius: 50%;
  background-color: #fff;
  object-fit: cover;
}
.bottom-category {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  width: 100%;
}
.bottom-category > h3 {
  color: var(--pink);
  font-size: 1.5rem;
}
.bottom-category > p {
  text-align: center;
  line-height: 1.5rem;
  font-size: 1rem;
  color: #777;
}
.bottom-category > button {
  background-color: transparent;
  border: 1px dashed var(--pink);
  border-radius: 0.2rem;
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  font-size: 1rem;
  color: var(--pink);
  width: 100%;
}

