header > .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
}
header > .container > h1 {
  font-family: "Dancing Script", "sans-serif";
  font-weight: bold;
}
header > .container > h1 > a {
